import React, { Component } from 'react';
import emailjs from '@emailjs/browser';

export default class ContactUs extends Component {
  render() {
    function enviarEmail(e){
      e.preventDefault();

      emailjs.sendForm('service_89dfm3e', 'template_f6swwhs', e.target, '6uoNye3a31e-yLHJI')
        .then((result) => {
            alert('Mensagem enviada com sucesso!')
        }, (error) => {
            alert(error.message)
        });
        e.target.reset(); //Limpa o campo quando envia o E-mail
    }

    let resumeData = this.props.resumeData;
    return (
      <section id="contact">
          <div className="row section-head">
            <div className="ten columns">
              <p className="lead">
              Sinta-se à vontade para entrar em contato comigo para qualquer trabalho ou sugestões
              </p>
              <form onSubmit={enviarEmail}>
              <div className="form-group">
              <label>Nome:</label>
      <input type="text" className="form-control" name="name" id="formGroupExampleInput" required placeholder="Digite seu Nome"/>
    </div>
    <div className="form-group">
   <label>Email:</label>
    <input type="email" className="form-control" name="user_email" id="exampleFormControlInput1" required placeholder="Digite o seu E-mail"/>
  </div>
  <div className="form-group">
    <label>Texto</label>
    <textarea className="form-control" name="message" id="exampleFormControlTextarea1" rows="3" required placeholder="Digite a sua mensagem"></textarea>
  </div>
  <button type="submit" className="submit">Enviar</button>
</form>
            </div>
          </div>
          <div className="row">
            <aside className="eigth columns footer-widgets">
              <div className="widget">
                <h4>Linked in :<a href={resumeData.linkedinId}> Afonso Ramon</a></h4>
              </div>
            </aside>
          </div>
        </section>
        );
  }
}